
.validation {
  background: transparent;
  font-size: 14px;
}
.validation .heading {
    font-size: 1.2em;
}
.validation th small {
    display: block;
}
.validation tbody tr.datas:hover {
    background: rgba(92, 156, 204, 0.25);
}
.validation .heading-days th {
    text-align: center;
}
.validation .heading-days th.locked {
      background: #CCC;
}
.validation td, .validation th {
    border-right: thin #ddd solid;
}
.validation th small {
    font-weight: 100;
    font-size: .8em;
}
.validation td:nth-child(odd) {
    background: rgba(204, 204, 204, 0.3);
}
.validation .datas th {
    padding-left: 1em;
}
.validation .datas td {
    text-align: right;
}



